import { mapGetters , mapActions } from 'vuex';

export default {
	data() {
		return {
			isLoadErr: false,
			dataObj: {},
		}
	},
	computed: {
		...mapGetters([
			'_portInfo'
		])
	},
	onLoad() {
		this.dataObj = this._portInfo
		this.toSaveSpotInfo(null)
	},
	methods: {
		...mapActions([
			'toSaveSpotInfo'
		])
	}
}
